
import axios from "axios";
import { mapGetters, mapActions } from "vuex";
import { isWeiXin } from "@/utils/comm.js";
import { api } from "@/utils/api.js";
import { wxPay } from "@/utils/wx_sdk.js";
import XHeader from 'vux/src/components/x-header/index.vue'


export default {
name: "",
props: {},
components: {
XHeader
},
data() {
return {
codeKey: "",
userName: "",
name: "",
phone: "",
store: "",
img1: "",
img2: "",
img1_sn: "",
img2_sn: "",
payFun: 1,
tips: false,
count: "365.00",
showRule: false,
ruleContent: "",

client: "", //alioss
fileSize: 500000,
iswx: isWeiXin(),
aliForm: ''
};
},
created() {
this.codeKey = this.$route.query.codeKey || "";
this.userName = this.$route.query.userName || "";
if (isWeiXin()) {
this.payFun = 1;
} else {
this.payFun = 2;
}
this.getGuize();
},
mounted() {},
methods: {

async pay() {

if(!this._check())return;
if(!this.img1_sn){
return this.$vux.toast.text("请重新上传身份证人像面");
}
if(!this.img2_sn){
return this.$vux.toast.text("请重新上传身份证国旗面");
}
let data = {
plat: 3,
account: this.account,
token: this.token,
shop_name: this.store,
real_name: this.name,
mobile: this.phone,
sn_up: this.img1_sn,
sn_down: this.img2_sn,
openid: this.user.openid,
pay_code: this.payFun, // 1微信 2支付宝 3银联
type: 3,
member_code: this.codeKey
};
const [err, res] = await api.addsmallshop(data);
if (err) {
this.$vux.toast.text(err.msg);
return;
}
if(res.code == 2000){
if(this.payFun == 1){
let success = res => {
this.$router.replace({
path: '/centerFull/partner/applyStatic',
query: {
status: 1
}
})
}
let fail = err => {
this.$vux.toast.text('支付失败');
}
wxPay(this, {...res.data.pay_param,success,fail})
}else{
let arr = res.data.pay_param.split('&');
let data = {};
arr.forEach(item => {
let Iarr = item.split('=');
data[Iarr[0]] = Iarr[1];
})
let params = {
notify_url: data.notify_url,
method: data.method,
partner: data.app_id,
_input_charset: data.charset,
sign_type: data.sign_type,
sign: data.sign,
notify_url: data.notify_url,
return_url: window.location.href,
body: data.biz_content
}
let html = `
<form action='https://mapi.alipay.com/gateway.do?_input_charset=utf-8' method='get'>
<input type="hidden" name="service" value="create_direct_pay_by_user" />
<input type="hidden" name="partner" value="${data.app_id}" />
<input type="hidden" name="_input_charset" value="${data.charset}" />
<input type="hidden" name="sign_type" value="${data.sign_type}" />
<input type="hidden" name="sign" value="${data.sign}" />
<input type="hidden" name="notify_url" value="${data.notify_url}" />
<input type="hidden" name="return_url" value="${window.location.href}" />
<input type="hidden" name="body" value="${data.biz_content}" />
</from>
`;
console.log(data)
this.aliForm = html;
this.$nextTick(()=>{
console.log(this.$refs.aliPay.children[0])
this.$refs.aliPay.children[0].submit();
})
}
}
console.log("pay", res);

},

_check() {
if (!this.name) {
this.$vux.toast.text("请填写姓名");
return;
}
let reg = /^1[0-9]{10}$/;
if (!reg.test(this.phone)) {
this.$vux.toast.text("请输入正确的手机号");
return;
}
if (!this.store) {
this.$vux.toast.text("请填写正确店铺名称");
return;
}
if (!this.img1 || !this.img2) {
this.$vux.toast.text("请上传证件");
return;
}
if (!this.tips) {
this.$vux.toast.text("请勾选协议");
return;
}
return true;

},

upimg(e, name) {
let that = this;
let file = e.target.files[0];
let data = {
plat: 3,
token: this.token,
account: this.account,
type: 17,
num: 1
};
if (name === "img1") {
this._ossImg(data, file, name);
this.img1 = window.URL.createObjectURL(file);
} else {
this._ossImg(data, file, name);
this.img2 = window.URL.createObjectURL(file);
}
},

async _ossImg(data, file, name) {
let that = this;
const [err, res] = await api.getpaths(data);
if (err) {
this.$vux.toast.text(err.msg);
return;
}
if (res.code == "2000") {
let config = {
secure: true,

accessKeyId: res.data.AccessKeyId,
accessKeySecret: res.data.AccessKeySecret,
bucket: res.data.bucket,
region: 'oss-cn-shenzhen',

}
console.log(res)
console.log(config)
this.client = new OSS.Wrapper(config);


this.client
.multipartUpload(res.data.files, file)
.then(function(result) {
if (name === "img1") {
that.img1_sn = res.data.sn;
} else {
that.img2_sn = res.data.sn;
}
console.log("result", result);
})
.catch(function(err) {
console.log("err", err);
console.log("err.name : " + err.name);
console.log("err.message : " + err.message);
});
}
},
async getGuize() {
let data = {
plat: 3,
account: this.account,
token: this.token,
type: 1
};
const [err, res] = await api.guize(data);
if (err) {
this.ruleContent = "看到我啦";
return;
}
this.ruleContent = res.data.list;
},

selectPayFun(type) {
switch (type) {
case 1:
this.payFun = 1;
break;
case 2:
this.payFun = 2;
break;
}
},

selectTips() {
this.tips = !this.tips;
},

ruleShow() {
this.showRule = true;
},
hiddenMask() {
this.showRule = false;
}






























},
computed: {
...mapGetters(["user", "account", "token"])
}
};



